*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  font-family: 'Work Sans', sans-serif;;
}

header{
  background-color: rgb(129, 228, 72);
  height: 100px;
  padding: 0 20px;
  margin-bottom: 10px;
}

h1{
  color: white;
  font-weight: 700;
  line-height: 100px;
  user-select: none;
}

.inputarea{
  display: inline-flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 10px;
  position: relative;
  
}

input{
  font-weight: 900;
}

textarea, input{
  width: 40vw;
  max-width: 450px;
  resize: none;
  outline: none;
  border: none;
  border-left: 5px solid orange;
  padding: 15px;
  color: orange;
  background-color: #efefef;
  text-transform: capitalize;
}

h2{
  margin-bottom: 5px;
}

.note{
  position: relative;
  width: 40%;
  max-width: 300px;
  height: 100px;
  margin-top: 10px;
  margin-top: 10px;
  margin-left: 20px;
  border-left: 5px solid orange;
  padding: 5px 5px 5px 10px;
  color: orange;
  background-color: #efefef;
  text-transform: capitalize;
}

.notearea{
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.add{
  position: absolute;
  border: none;
  outline: none;
  color: rgb(59, 238, 59);
  font-weight: 900;
  bottom: 5px;
  right: 5px;
  background: transparent;
  letter-spacing: 1px;
  cursor: pointer;
}

.add:hover{
  background-color: orange;
}

@media all and (max-width: 450px) {
  textarea, input{
    display: block;
    width: 100%;
    resize: none;
    outline: none;
    border: none;
    border-left: 5px solid orange;
    padding: 15px;
    color: orange;
    background-color: #efefef;
    text-transform: capitalize;
  }

  .inputarea{
    margin: 0 auto;
    background-color: red;
    display: flex;
    width: 90vw;
    flex-direction: column;
    align-items: center;
    position: relative;
    
  }

  .notearea{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .note{
    position: relative;
    width: 90vw;
    height: 100px;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 20px;
    border-left: 5px solid orange;
    padding: 5px 5px 5px 10px;
    color: orange;
    background-color: #efefef;
    text-transform: capitalize;
  }
  
}